#dashboard {
  position: sticky;
  width: 100%;
  height: 100px;
}

#search-form {
  position: absolute;
  width: 620px;
  height: 44px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#searchbar {
  position: absolute;
  width: 370px;
  height: 44px;
  left: 0%;
  top: 50%;
  transform: translate(0%, -50%);
  border: 7.5px double brown;
  font-size: 25px;
  padding: 0px 3px;
}

#type-selector {
  position: absolute;
  width: 120px;
  height: 44px;
  left: 375px;
  top: 50%;
  transform: translate(0%, -50%);
  border: 7.5px double brown;
  font-size: 16px;
}

#country-selector {
  position: absolute;
  width: 120px;
  height: 44px;
  right: 0%;
  top: 50%;
  transform: translate(0%, -50%);
  border: 7.5px double brown;
  font-size: 16px;
}

#mapImg {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 5px solid brown;
}
